import Dialog from '../../wxcomponents/vant/dist/dialog/dialog';
var app = getApp().globalData
export default {
	data() {
		return {
			show:true,
			city:'哈尔滨',
			longitude:'',
			latitude:'',
			cate_id:'',
			page:1,
			shopList:[],
			list:[],
			loading:false,
			allowLoad:false,
			title:'全部',
			showTitle:false
		}
	},
	onShareAppMessage:function(res) {
		return {
		  title: '德申汇本地生活',
		  imageUrl:app.shareImg,
		  path: '/pagesMy/LocalLife/LocalLife'
		}
	},
	onPullDownRefresh(){
		this.page = 1
		this.getData()
		setTimeout(function () {
		        uni.stopPullDownRefresh();
				uni.showToast({
				    title: '刷新成功',
				    duration: 1000,
					icon:'none'
				});
		  }, 1000);
	},
	// 触底加载
	onReachBottom(){
		if(this.allowLoad){
			this.allowLoad = false				
			this.loading = true
			this.page++
			this.getData()
			// setTimeout(() => {
				
			// 	uni.showToast({
			// 		title: '加载成功',
			// 		duration: 1000,
			// 		icon:'none'
			// 	});
			// }, 1500)	
		}
	},
	onShow() {
		
		
	},
	onLoad(e) {
		var that = this
		uni.getLocation({
		    type: 'wgs84',
		    success: function (res) {
				that.longitude = res.longitude
				that.latitude = res.latitude
				that.getData()
				that.getCategory()
		    }
		});
		this.city = uni.getStorageSync('city')
		uni.showLoading({
		    title: '加载中'
		});
	},
	methods: {
		// 跳转个人主页
		MyInfo(id){
			uni.navigateTo({
				url:'../../pagesMy/MyInfo/MyInfo?id='+id
			})
		},
		phone(){
			Dialog.confirm({
			  title: '入驻电话',
			  message:'拨打德申小助手电话?'
			}).then(() => {
				uni.makePhoneCall({
				    phoneNumber: '15645088075' //仅为示例
				});
			})
			
		},
		tel(it){
			uni.makePhoneCall({
			    phoneNumber: it.mobile //仅为示例
			});
		},
		map(it){
			uni.openLocation({
			    latitude: Number(it.latitude),
			    longitude: Number(it.longitude),
			    success: function () {
			        console.log('success');
			    }
			});
			
		},
		yulan(it,ind){
			console.log(it)
			var arr = []
			it.forEach((item,index)=>{
				arr.push(item.src)
			})
			 uni.previewImage({
				urls: arr,
				current:ind
			});
		},
		bindPickerChange(e){
			this.city = e.detail.value[1].replace(/省|市|自治区|特别行政区|壮族|回族|维吾尔|城区/g, '')
			this.page = 1
			this.$nextTick(()=>{
				this.getData()
			})
		},
		category(it){
			this.cate_id = it.id
			this.page = 1
			this.getData()
			this.title = it.name
			this.show = true
		},
		getCategory(){
			this.$api.local_life_category_list({
			}).then(res=>{
				this.list = res.data.data
				this.list.unshift({
						"id": '',
						"name": "全部"
					})
			})
		},
		getData(){
			this.$api.local_life_list({
				token: uni.getStorageSync("token"),
				user_id: uni.getStorageSync("userId"),
				longitude:this.longitude,
				latitude:this.latitude,
				city:this.city,
				cate_id:this.cate_id,
				page:this.page
			}).then(res=>{
				uni.hideLoading();
				if(res.data.code=== '200'){
					if(this.page===1){
						this.shopList = res.data.data
						if(res.data.data.length>=20){
							this.allowLoad= true
							this.loading =false
							this.showTitle = false
						}else{
							this.allowLoad= false
							this.loading =false
							this.showTitle = true
						}
					}else{
						this.shopList =this.shopList.concat(res.data.data) 
						if(res.data.data.length>=20){
							this.allowLoad= true
							this.loading =false
							this.showTitle = false
						}else{
							this.allowLoad= false
							this.loading =false
							this.showTitle = true
						}
					}
					
				}
			})
		},
		all(){
			this.show = !this.show
		}
	}
}
